<template>
    <div class="shopcart">
		<div class="conent" @click='toggleList'>
			<div class="conent-left">
				<div class="logo-wrapper">
					<div class="logo" :class="{'hightlight' : totalCount > 0}">
						<i class="icon-shopping_cart" :class="{'hightlight' : totalCount > 0}"></i>
					</div>
					<div class="num" v-show='totalCount>0'> {{totalCount}} </div>
				</div>
				<div class="price" :class="{'hightlight' : totalCount > 0}">
					¥{{totalPrice}}
				</div>
				<div class="desc">另需配送费¥{{deliveryPrice}} 元 </div>
			</div>
			<div class="conent-right">
				<div class="pay" :class="payClass">
					{{payDesc}}
				</div>
			</div>
			<div class="ball-container">
			<div v-for="ball in balls">
				<transition  name='drop'
							v-on:before-enter = 'beforeEnter'
							v-on:enter = 'enter'
							v-on:after-enter = 'afterEnter'
				>
					<div class="ball" v-show='ball.show' >
						<div class="inner inner-hook"></div>
					</div>
				</transition>
			</div>
		</div>
		</div>
		<transition name='fold'>
			<div class="shopcart-list" v-show='listShow'>
			<div class="list-header">
				<h1 class="title">购物车</h1>
				<span class="empty" @click='empty'>清空</span>
			</div>
			<div class="list-content" ref='listContent'>
				<ul>
					<li class="food" v-for='food in selectFoods'>
						<span class="name"> {{food.name}} </span>
						<div class="price">
							<span>¥{{food.price*food.count}} </span>
						</div>
						<div class="cartcontrol-wrapper">
							<cartcontrol :food='food'></cartcontrol>	
						</div>
					</li>
				</ul>
			</div>
			</div>
		</transition>
    </div>
    <!-- <transition name='fad'>
    <div class="list-mask" v-show='listShow'>
    	
    </div>
    </transition> -->
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll';
import cartcontrol from '../cartconcontrol/cartcontrol.vue'
    export default {
    	data() {
    		return {
    			balls : [{
    				show : false
    			},{
    				show : false
    			},{
    				show : false
    			},{
    				show : false
    			},{
    				show : false
    			}],
    			dropball : [],
    			fold : false
    		}
    	},
    	props : {
    		selectFoods : {
    			type : Array,
    			default() {
    				return[
    					{
    						price : 30,
    						count:1
    					}
    				];
    			}
    		},
    		deliveryPrice : {
    			type : Number,
    			default: 0
    		},
    		minPrice : {
    			type : Number,
    			default : 0
    		}
    	},
    	computed : {
    		totalPrice() {
    			let total = 0;
    			// console.log(total)
    			this.selectFoods.forEach((food)=>{
    				total += food.price * food.count;
    				// console.log(total)
    			})
    			return total;
    		},
    		totalCount() {
    			let count = 0;
    			this.selectFoods.forEach((food)=>{
    				count += food.count;
    			})
    			return count
    		},
    		payDesc() {
    			if(this.totalPrice === 0) {
    				return `¥${this.minPrice}元起送`
    			}else if (this.totalPrice < this.minPrice) {
    				let diff = this.minPrice - this.totalPrice
    				return `还差¥${diff}元起送`
    			}else{
    				return '去结算'
    			}
    		},
    		payClass() {
    			if(this.totalPrice < this.minPrice) {
    				return 'not-enough'
    			}else{
    				return 'enough'
    			}
    		},
    		listShow() {
    			if(!this.totalCount){
    				this.fold = true
    			}
    			let show = !this.fold;
    			if(show){
    				if(!this.scroll){
						this.$nextTick(() =>{
    						this.scroll = new BScroll(this.$refs.listContent,{
    							click : true
    					})
    				})
    				}else{
    					this.scroll.refresh()
    				}
    				
    			}
    			return show
    		}
    	},
    	methods : {
    		drop(el) {
    			// console.log(el)
    			for(let i = 0 ; i < this.balls.length ; i++) {
    				let ball = this.balls[i];
    				if (!ball.show){
    					ball.show = true;
    					ball.el = el;
    					this.dropball.push(ball);
    					return;
    				}
    			}
    			// console.log(this.$store.state.el)
    		},
    		beforeEnter(el){
    			
    			let count = this.balls.length;
    			while (count--) {

    				let ball = this.balls[count];
    				if( ball.show ) {

						let rect = ball.el.getBoundingClientRect();

						let x = rect.left - 32;
						let y = -(window.innerHeight - rect.top - 22);
						

						el.style.webkitTransform = 'translate3d(0,'+ y +'px,0)';
						el.style.transform = 'translate3d(0,'+ y +'px,0)';
						let inner = el.getElementsByClassName('inner-hook')[0];
						inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
						inner.style.transform = `translate3d(${x}px,0,0)`;
						// console.log(el)
    				}
    			
    			}
    		},
    		enter(el) {
    			
    			let rf = el.offsetHeight;
    			this.$nextTick(()=>{
    				el.style.webkitTransform = 'translate3d(0,0,0)';
						el.style.transform = 'translate3d(0,0,0)';
						let inner = el.getElementsByClassName('inner-hook')[0];
						inner.style.webkitTransform = 'translate3d(0,0,0)';
						inner.style.transform = 'translate3d(0,0,0)';
    			})
    		},
    		afterEnter(el) {

    			let ball = this.dropball.shift();
    			if(ball) {
    				ball.show = false;
    				el.style.display = 'none'
    			}
    		},
    		toggleList() {
    			if(!this.totalCount){
    				return;
    			}
    			this.fold = !this.fold
    		},
    		empty() {
    			this.selectFoods.forEach((food) => {
    				food.count = 0;
    			})
    		}
    	},
    	components : {
    		cartcontrol
    	}
    }
</script>

<style >
	.shopcart{
		position: fixed;
		left: 0;
		bottom:0;
		z-index: 50;
		width: 100%;
		height: 48px;
		/*background: #000;*/
	}
	.shopcart .conent{
		display: flex;
		background: #141d27;
	}
	.shopcart .conent .conent-left{
		flex: 1;
		font-size: 0;
	}
	.shopcart .conent .conent-right{
		flex: 0 0 105px;
		width: 105px;
	}
	.shopcart .conent .conent-left .logo-wrapper{
		display: inline-block;
		position: relative;
		top: -10px;
		margin: 0 12px;
		/*font-size: 24px;*/
		padding: 6px;
		width: 56px;
		height: 56px;
		box-sizing: border-box;
		border-radius: 50%;
		background: #141d27;

	}
	.shopcart .conent .conent-left .logo-wrapper .num{
		position: absolute;
		top: 0;
		right: 0;
		width: 24px;
		height: 16px;
		line-height: 16px;
		text-align: center;
		border-radius: 16px;
		font-size: 9px;
		font-weight: 700px;
		color: #fff;
		background: rgb(240,20,20);
		box-shadow: 0 4px 8px 0 rgba(0,0,0,.4)
	}
	.shopcart .conent .conent-left .logo-wrapper .logo{
		width: 100%;
		height: 100%;
		border-radius: 50%;
		background: #2b343c;
		text-align: center;
	}
	.shopcart .conent .conent-left .logo-wrapper .logo.hightlight{
		background: rgb(0,160,220);
	}
	.shopcart .conent .conent-left .logo-wrapper .logo.hightlight .icon-shopping_cart.hightlight{
		color: #fff;
	}

	.shopcart .conent .conent-left .price{
		display: inline-block;
		vertical-align: top;
		line-height: 24px;
		margin-top: 12px;
		box-sizing: border-box;
		padding-right: 12px;
		border-right: 1px solid rgba(255,255,255,.1);
		font-size: 16px;
		font-weight: 700;
		color: rgba(255,255,255,.4);
	}
	.shopcart .conent .conent-left .price.hightlight{
		color: #fff;
	}
	.shopcart .conent .conent-left .desc{
		display: inline-block;
		vertical-align: top;
		line-height: 24px;
		margin:12px 0 0 12px;
		font-size: 10px;
		color: rgba(255,255,255,.4);
	}
	.shopcart .conent .conent-left .logo-wrapper .logo .icon-shopping_cart{
		font-size: 24px;
		line-height: 44px;
		color: #80858a;

	}
	.shopcart .conent .conent-right .pay{
		height: 48px;
		line-height: 48px;
		text-align: center;
		font-size: 12px;
		color: rgba(255,255,255,.4);
		font-weight: 700;
		background: #2b333b;
	}
	.shopcart .conent .conent-right .pay.not-enough{
		background: #2b333b;
	}
	.shopcart .conent .conent-right .pay.enough{
		background: #00b34c;
		color: #fff;
	}


	.shopcart .conent .ball-container{

	}
	.shopcart  .ball-container .ball{
		position: fixed;
		left: 32px;
		bottom: 22px;
		z-index: 200;
		transition: all 0.4s cubic-bezier(.49,-0.29,0.75,0.41);

	}
	.shopcart  .ball-container .ball .inner{
		width: 16px;
		height: 16px;
		border-radius: 50%;
		background: rgb(0,160,220);
		transition: all 0.4s linear;
	}
	/*.move-enter-active,
	.move-leave-active{
		opacity: 1;
		transform: translate3d(0,0,0);

	}
	.move-enter-active .inner,
	.move-leave-active .inner{
		transition: all 0.4s linear;
		transform: rotate(0);
	}*/

	.shopcart .shopcart-list{
		position: absolute;
		top: 0;
		z-index: -1;
		left: 0;
		width: 100%;
		transition: all 0.5s ;
		/*transform: translate3d(0,-100%,0);*/
		
	}
	.fold-enter-active{
		transition: all 0.5s;
		/*transform: translate3d(0,-100%,0);*/
	}
	
	
	.fold-enter,
	.fold-leave{
		transform: translate3d(0,-100%,0);
		/*transform: translate3d(0,0,0);*/
	}
	
	/*.fold-enter,			
	.fold-leave-active{
		transform: translate3d(0,-100%,0);
	}
	.fold-enter-active,
	.fold-leave{
		transform: translate3d(0,0,0);
	}*/

	.shopcart .list-header{
		height: 40px;
		line-height: 40px;
		padding: 0 18px;
		background: #f3f5f7;
		border-bottom: 1px solid rgba(7,17,27,.1);
	}
	.shopcart .list-header .title{
		float: left;
		font-size: 14px;
		color: rgb(7,17,27);

	}
	.shopcart .list-header .empty{
		float: right;
		font-size: 12px;
		color: rgb(0,160,220);
	}
	.shopcart .list-content{
		padding:0 18px;
		max-height: 217px;
		background: #fff;
		overflow:hidden;

	}
	.shopcart .list-content .food{
		position: relative;
		padding:12px 0;
		box-sizing: border-box;
		border-bottom: 1px solid rgba(7,17,27,.1);

	}
	.shopcart .list-content .food .name{
		line-height: 24px;
		font-size: 14px;
		color: rgb(7,17,27);
	}
	.shopcart .list-content .food .price{
		position: absolute;
		right: 90px;
		bottom: 12px;
		line-height: 24px;
		font-size: 14px;
		font-weight: 700;
		color: rgb(240,20,20);
	}
	.shopcart .list-content .food .cartcontrol-wrapper{
		position: absolute;
		right: 0;
		bottom: 6px;
	}
</style>



